/*recommended*/
.recommended{
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
    background-size: cover;
    color: white;
    text-align: center;
    align-items: center;
}

.recommended-grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 95%;
    justify-items: center;
    align-items: center;
    padding: 2.5em;
    margin: auto 
}

.first{
    order:1;
}

.first header{
    font-size: 3rem;
    font-weight: 100;
    letter-spacing: 5px;
}

.first p{
    font-size: 1rem;
    font-weight: 100;
    letter-spacing: 5px;
}

.second{
    order:2;
}

.img-group{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap:20px ;
    justify-items: center;
    align-items: center;
    margin: auto;
}

.img-group img{
    width: 100%;
    height: 16rem;
    box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.4), 0 6px 23px 0 rgba(0, 0, 0, 0.19);
}
